import React, { SelectHTMLAttributes } from "react";
import { FC } from "react";
import DropdownArrowIcon from "../Icons/DropdownArrowIcon/DropdownArrowIcon";
import { StyledDropdown, StyledDropdownInput } from "./styled";

export const DropdownInput:FC<SelectHTMLAttributes<{}>> = ({children, ...props}) => {
    return (<StyledDropdown>
        <StyledDropdownInput {...props}>
            {children}
        </StyledDropdownInput>
        <DropdownArrowIcon></DropdownArrowIcon>
    </StyledDropdown>)
}