import * as React from "react";
import { Helmet } from "react-helmet";
import { Layout, Text, Divider } from "../components/common";
import { StaticImage } from "gatsby-plugin-image";
import {
  BackgroundImage,
  ContactUsPageHeader,
  InfoSection,
  MobileBackground,
  StyledContactUsFormContainer,
} from "../components/contact-us/styled";
import { ContactUsForm } from "../components/contact-us";
import SubscribeBlock from "../components/common/SubscribeBlock";
import { ContentContainer } from "../components/common/Container/Container";

const AboutUsPage = () => {
  return (
    <>
      <Helmet
        title={"Telgorithm | Contact Us"}
      />
      <Layout disabledContainer>
        <MobileBackground>
          <StaticImage
            src="../images/ContactUs/mobileBackground.png"
            alt="Background"
            objectFit="cover"
          ></StaticImage>
        </MobileBackground>
        <ContentContainer>
          <ContactUsPageHeader>
            <Text.Title.Bold.xl>
              Ready to supercharge your messaging ROI? Let's chat!
            </Text.Title.Bold.xl>
            <Text.Regular.Bold.md>
              Complete the form below and we'll be in touch.{" "}
            </Text.Regular.Bold.md>
          </ContactUsPageHeader>
          <StyledContactUsFormContainer>
            <div>
              <ContactUsForm></ContactUsForm>
            </div>
            <BackgroundImage>
              <StaticImage
                src="../images/ContactUs/background.png"
                alt="Background"
                objectFit="cover"
              ></StaticImage>
            </BackgroundImage>
          </StyledContactUsFormContainer>
          <InfoSection>
            <SubscribeBlock
              title={"Get the messaging know-hows.."}
              description={
                "Navigate the cloud communications world with knowledge. Subscribe to our newsletter to receive the latest industry news, tips, and updates from our team."
              }
            />
          </InfoSection>
        </ContentContainer>
      </Layout>
    </>
  );
};

export default AboutUsPage;
