import styled from "styled-components";
import { colors } from "../../../utils/colors";

export const StyledDropdown = styled.div`
  position: relative;
  svg {
    position: absolute;
    top: 15px;
    right: 10px;
  }
`;

export const StyledDropdownInput = styled.select`
  height: 36px;
  padding: 0 15px;
  cursor: pointer;
  appearance: none;
  option {
    font-family: "Rubik";
    color: ${colors.primary.black};
    background-color: none;
  }
  width: 100%;
  box-sizing: border-box;
  background-color: ${colors.primary.white};
  color: ${colors.primary.black};
  border: 1px solid ${colors.secondary.gray300};
  font-family: "Rubik";
  border-radius: 5px;
  &::placeholder {
    color: ${colors.primary.gray500};
    font-family: "Rubik";
  }
`;
