import styled from "styled-components";
import { colors } from "../../utils/colors";
import { device } from "../../utils/layoutBreakpoints";

export const ContactUsPageHeader = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 90%;
  margin: 0 auto;
  text-align: center;

  @media ${device.laptop} {
    width: 70%;
  }
`;

export const StyledContactUsForm = styled.form`
  font-size: 0.8rem;
  line-height: 1.1rem;
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  label {
    font-weight: 500;
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 100%;
  }

  input,
  textarea {
    width: 100%;
    box-sizing: border-box;
    background-color: ${colors.primary.white};
    color: ${colors.primary.black};
    border: 1px solid ${colors.secondary.gray300};
    font-family: "Rubik";
    border-radius: 5px;
    &::placeholder {
      color: ${colors.primary.gray500};
      font-family: "Rubik";
    }
  }

  input {
    height: 36px;
    padding: 10px 15px;
  }

  textarea {
    height: 100px;
    padding: 10px 15px;
    resize: none;
  }

  fieldset {
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
    legend {
      font-weight: 500;
      margin-bottom: 10px;
    }
    label {
      cursor: pointer;
      background: ${colors.primary.white};
      border: 1px solid ${colors.secondary.gray300};
      border-radius: 8px;
      padding: 10px 15px;
      text-align: center;
      box-sizing: border-box;
    }
    input {
      display: none;
      &:checked + label {
        color: ${colors.primary.white};
        background-color: ${colors.primary.blue};
      }
    }

    @media ${device.tablet} {
      flex-direction: row;
    }
  }
`;

export const StyledContactUsFormContainer = styled.div`
  display: flex;
  background-color: ${colors.secondary.gray100};
  border-radius: 16px;
  justify-content: space-between;
  align-items: center;
  margin-top: 35px;

  & > div:first-child {
    padding: 25px 25px;
    width: 100%;
    box-sizing: border-box;
  }

  @media ${device.tablet} {
    & > div:first-child {
      padding: 50px 50px;
    }
  }

  @media ${device.laptop} {
    margin-top: 65px;
    & > div:first-child {
      padding: 50px 50px;
      width: 50%;
      box-sizing: border-box;
    }
  }

  @media ${device.desktop} {
    & > div:first-child {
      padding: 50px 100px;
    }
  }
`;

export const BackgroundImage = styled.div`
  height: 100%;
  width: 50%;
  box-sizing: border-box;
  object-fit: cover;
  height: 820px;
  display: none;
  @media ${device.laptop} {
    display: block;
  }
  div {
    width: 100%;
    height: 100%;
  }
  img {
    width: 100%;
    height: 100%;
    border-radius: 0 16px 16px 0;
  }
`;

export const FormUsername = styled.div`
  display: flex;
  flex-direction: column;
  gap: 15px;
  @media ${device.tablet} {
    flex-direction: row;
  }
`;


export const InfoSection = styled.div`
  margin-top: 20px;
  @media ${device.tablet} {
    flex-direction: row;
    input {
      width: 25%;
    }
  }
`;

export const MobileBackground = styled.div`
    display: block;
    @media ${device.laptop} {
        display: none;
    }
`
